const SideBar = (props)=>{
    const[sidebarClass,setSidebarClass]=useState(props.sidebar)
    const closeHandler = (e) =>{
        e.preventDefault()
        setSidebarClass("sidebar close")
        setTimeout(()=>{props.close()},1000)
    }

    return(
            <Plus className ={sidebarClass}>
                <X id="close" className={sidebarClass} onClick={closeHandler}>X</X>
                 <ul className="alllist" >
                <li><a href="#">서울시 자전거 이용환경 만족도 통계</a></li>
                <li><a href="#">서울시 자전거도로 현황 통계</a></li>
                <li><a href="#">서울시 자전거 이용률 통계</a></li>
                <li><a href="#">서울시 공공자전거 대여소 정보</a></li>
                <li><a href="#">서울시 공공자전거 고장 신고 내역</a></li>
                <li><a href="#">서울시 공공자전거 신규가입자 정보</a></li>
                <li><a href="#">서울시 지도 태깅 자전거 편의시설 정보</a></li>
                <li><a href="#">서울시 자전거 교통사고 통계</a></li>
                <li><a href="#">서울시 중앙분리대 및 무단횡단 방지시설 현황</a></li>
                <li><a href="#">서울시 안전 표지 관련 정보</a></li>
                <li><a href="#">도로교통공단 교통사고 정보</a></li>
                              
                </ul>
            </Plus>
 
    );}
    const Toggle =(props)=>{
                
        return(
    
           <Menubar id="toggle" onClick={props.click}>
                    <Line className='line'></Line>
                     <Line className='line'></Line>
                     <Line className='line'></Line>
                </Menubar>
           
        );
    }
const Layout=()=>{
    const[sidebarOpen, setSidebarOpen]= useState(false)
    
    const openHandler =() =>{
        if(!sidebarOpen){
            setSidebarOpen(true)
        }else{
            setSidebarOpen(false)
        }
    }
    const sidebarCloseHandler= ()=>{
        setSidebarOpen(false)

    }
    let sidebar
    if(sidebarOpen){
        sidebar = <SideBar close={sidebarCloseHandler} sidebar={'sidebar'}></SideBar>
    }
    return(
     <Fragment>
        {sidebar}
       
         <Toggle click={openHandler} />
       
     </Fragment>
    )
}

